import React, { Component } from 'react';
import LoginPage from '../LoginPage/LoginPage';
import BingoSheet from '../BingoSheet/BingoSheet';


class WelcomePage extends Component {

    state = {
        isLoggedIn: false
    }
    
    componentDidMount() {
        console.log("WelcomePage did mount.");

        if (!this.props.name) {
            console.log("no user logged in. is the user registered?");
        } else {
            this.setState((prevState) => ({
                isLoggedIn: true
            }))
            console.log(this.state.name);
        }


    }

    async getPlayerData (name) {
        let player = undefined;
        let requestUrl = `http://localhost:4243/find/${this.props.name}`;
        let data = fetch(requestUrl, {
            method: 'get'
        })
        .then( (res) => {
            return res.json();
        })
        .then( (data) => {
            player = data;
            return player;
        })
        return player;
    }

    componentDidUpdate() {
        console.log("WelcomePage did update.");
    }

    render() {
        return (
            <div id="welcomePage">
                <div id="welcomePageContent">
                    <div>
                        { !this.state.isLoggedIn ?
                         <LoginPage buttonText={this.state.buttonText} login={this.logUserIn} /> 
                        : 
                         <BingoSheet name={this.state.name} />
                        }  
                    </div>
                </div>
            </div>
        )
    }
}

export default WelcomePage;