import React, { Component } from 'react';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import BingoSheet from '../BingoSheet/BingoSheet';
import BingoKey from '../BingoKey/BingoKey'
import WelcomePage from '../WelcomePage/WelcomePage';
import NavBar from '../NavBar/NavBar';

class ReadingBingo extends Component {

    state = {
        name: undefined,
        accountType: undefined,
        isLoggedIn: false
    }

    componentDidMount() {
        console.log("ReadingBingo did mount.");
        this.setState(() => ({
            name: localStorage.getItem("name")
        }))
    }

    componentDidUpdate() {
        console.log("ReadingBingo did update.");
    }

    logUserIn = (event) => {
        event.preventDefault();
        const name = event.target.elements.firstName.value.trim();
        const accountType = event.target.elements.accountType.value;
        localStorage.setItem('name',name);
        localStorage.setItem('accountType',accountType);
        if (localStorage.getItem("name") && localStorage.getItem('accounttype')) {
            this.setState((prevState) => ({
                isLoggedIn: true
            }))
        }
    }

    render() {
        return (
            <div id="readingBingo">
                <BrowserRouter>
                    <NavBar name={this.state.name} />
                    <Routes>
                        <Route exact path="/" element={<WelcomePage name={this.state.name} />} />
                        {this.state.name && <Route exact path="/sheet" element={<BingoSheet name={this.state.name} />} />}
                        {this.state.name && <Route exact path="/key" element={<BingoKey name={this.state.name} />} />}
                    </Routes>
                </BrowserRouter>
            </div>
        );
    } 

}

export default ReadingBingo;